<template>
	<div class="tw-menu">
		<template v-for="item in data">
			<tw-menu-group :data="item" :active="active" v-if="item.children && item.children.length > 0"></tw-menu-group>
			<tw-menu-item :data="item" :active="active" v-else></tw-menu-item>
		</template>
	</div>
</template>

<script>
	import TWMenuGroup from './TWMenuGroup'
	import TWMenuItem from './TWMenuItem'

	export default {
		name: "TWMenu",
		data() {
			return {
				active: '',
			}
		},
		props: {
			data: Array,
			height: String,
		},
		mounted() {
			this.$nextTick(() => {});
		},
		computed: {
			menuData() {
			}
		},
		watch: {
			'$route'(value) {
				this.active = value.path;
			}
		},
		components: {
			'tw-menu-group': TWMenuGroup,
			'tw-menu-item': TWMenuItem
		}
	}
</script>

<style lang="scss" scoped>
	.tw-menu {
		height: 100%;
		color: #cccccc;
		background-color: #189aff;

		&-item {
			height: 40px;
			line-height: 40px;
			cursor: pointer;
			&:hover {
				color: #ffffff;
				background-color: rgba(255, 255, 255, .2);
			}
			&.active {
				color: #ffffff;
				background-color: #0d72d3;
			}
		}
		&-link {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
</style>