			<tw-icon :icon="data.icon"></tw-icon>{{data.title}}
		</router-link>
	</div>
</template>

<script>
	import TWIcon from './TWIcon'

	export default {
		name: "TWMenuItem",
		props: {
			data: Object,
			active: String
		},
		components: {
			'tw-icon': TWIcon
		}
	}
</script>

