			height: 40px;
			line-height: 40px;
			cursor: pointer;

			&:hover {
				color: #ffffff;
				background-color: rgba(255, 255, 255, .2);
			}

			&.active {
				color: #ffffff;
				background-color: #0d72d3;
			}
		}

		&-link {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	.tw-icon {
		margin-right: 5px;
	}
</style>