<template>
	<tw-layout type="template">
		<template slot="layout-header">
			<tw-header>
				<template slot="title"><div class="tw-logo-title">电缆普查后台管理系统</div></template>
				<template slot="tool" class="system-toolbar">
					<!--<div class="system-toolItem"><tw-icon icon="icon-user" font-size="24px"></tw-icon></div>-->
					<div class="system-toolItem" @click="handleDropOutClick"><tw-icon icon="icon-close" font-size="24px"></tw-icon></div>
				</template>
			</tw-header>
		</template>
		<template slot="layout-left">
			<tw-menu :data="menuData" height="100%"></tw-menu>
		</template>
		<template><router-view/></template>
	</tw-layout>
</template>

<script>
	import TWLayout from '../components/TWLayout'
	import TWHeader from '../components/TWHeader'
	import TWIcon from '../components/TWIcon'
	import TWMenu from '../components/TWMenu'
	import axios from 'axios'

	export default {
		name: "Index",
		data() {
			return {
				menuData: [
					{id: 'gzzjk', title: '工作组监控', icon: 'icon-user', html: '/gzzjk'},
					{id: 'pcgjgl', title: '普查轨迹管理', icon: 'icon-user', html: '/pcgjgl'},
					{id: 'zhtj', title: '综合统计', icon: 'icon-user', html: '/zhtj'},
					{id: 'yhgl', title: '用户管理', icon: 'icon-user', html: '/yhgl'}
				]
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.getLogin()
			});
		},
		components: {
			'tw-layout': TWLayout,
			'tw-header': TWHeader,
			'tw-menu': TWMenu,
			'tw-icon': TWIcon
		},
		methods: {
			getLogin() {
				const baseURL = this.baseURL
				axios.get('company/getlogin', {
					baseURL
				}).then(res => {
					let info = res.data.info;
					if (info === 'null') this.$router.push({name: 'login'});
				})
			},
			handleDropOutClick() {
				this.$router.push({name: 'login'});
			}
		}
	}
</script>

